
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Blur from 'vux/src/components/blur/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
components: {
Flexbox, FlexboxItem, XInput, Group, XButton, Alert, Divider, Blur
},
data () {
return {
Loading: false,  //loading
disabled: false, //
userName: '',
passWord: '',
userNameValue: '',
passWordValue: '',
show: false, 	//
title: "",  //title
content: '', 	//
Logo: "http://appdev.pujiajia.com/static/mobile/images/app_ico_touch.png",
LoginBg: "https://o3e85j0cv.qnssl.com/tulips-1083572__340.jpg",
userNamePlaceholder: "",
passwordPlaceholder: "",
VerinFormation: " V1.0.1"
}
},
methods: {

submitValid(){
this.Loading = true;
this.disabled = true;
if(this.userNameValue!=""&&this.passWordValue!=""){
this.$router.push('/index');
}else{
this.show = true;
this.content = "";
this.Loading = false;
this.disabled = false;
}
}
}
}



